body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
    font-family: 'Quicksand';
    font-style: normal;
    font-weight: 500;
    src: url(/static/media/Quicksand-VariableFont_wght.f9baef8a.ttf); /* IE9 Compat Modes */
    src: local('Quicksand'), local('Quicksand'),
         url(/static/media/Quicksand-VariableFont_wght.f9baef8a.ttf) format('truetype'), /* Safari, Android, iOS */
  }
.blog-shadow-dreamy {
    box-shadow: 0 1px 2px rgba(0,0,0,0.07), 
                0 2px 4px rgba(0,0,0,0.07), 
                0 4px 8px rgba(0,0,0,0.07), 
                0 8px 16px rgba(0,0,0,0.07),
                0 16px 32px rgba(0,0,0,0.07), 
                0 32px 64px rgba(0,0,0,0.07);
}

.bg-green {
    background-color: #024823;
}
.cornerLogo{
    width: 400;
    height: 100;
    /* fill: #FFFFFF; */
}
.fillWhite{
    fill: #FFFFFF;
}

.webmap {
    height: 800px;
    margin-top: 10px;
    border-radius: 5px;
}

.hvr-grow {
    display: inline-block;
    vertical-align: middle;
    transform: translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    /* backface-visibility: hidden; */
    -moz-osx-font-smoothing: grayscale;
    transition-duration: 0.3s;
    transition-property: transform;
}

.hvr-grow:hover,
.hvr-grow:focus,
.hvr-grow:active {
    transform: scale(1.02);
    cursor: pointer;
}
